import s from "./TableBlock.module.scss";
import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

function createData(address, avaliable, quantity) {
    return { address, avaliable, quantity};
}

const rows = [
    createData('–ú–æ—Å–∫–≤–∞, —É–ª. –ù–∞—É–∫–∏ 25', '–í –Ω–∞–ª–∏—á–∏–∏', 1),
    createData('–ú–æ—Å–∫–≤–∞, —É–ª.  –Æ–∂–Ω–∞—è 134', '–í –Ω–∞–ª–∏—á–∏–∏', 2),
    createData('–°–∞–Ω–∫—Ç-–ü–µ—Ç–µ—Ä–±—É—Ä–≥, —É–ª. –ö—Ä–∞—Å–Ω–∞—è 19', '–ù–µ—Ç –≤ –Ω–∞–ª–∏—á–∏–∏', 0),
    createData('–ö–∏–µ–≤, —É–ª –®–µ–≤—á–µ–Ω–∫–æ 167', '–ù–µ—Ç –≤ –Ω–∞–ª–∏—á–∏–∏', 0),
];

export default function TableBlock() {
    return (
        <TableContainer component={Paper}>
            <Table sx={{ maxWidth: 1134 }} aria-label="simple table">
                <TableHead className={s.head}>
                    <TableRow>
                        <TableCell style={{borderBottom:"none", borderTop:"none"}}>–ê–¥—Ä–µ—Å</TableCell>
                        <TableCell style={{borderBottom:"none", borderTop:"none"}} align="left">–†–µ–∂–∏–º —Ä–∞–±–æ—Ç—ã</TableCell>
                        <TableCell style={{borderBottom:"none", borderTop:"none"}} align="left">–î–æ—Å—Ç—É–ø–Ω–æ</TableCell>
                        <TableCell style={{borderBottom:"none", borderTop:"none"}} align="left">–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ</TableCell>
                        <TableCell style={{borderBottom:"none", borderTop:"none"}} align="left"></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody className={s.body}>
                    {rows.map((row) => (
                        <TableRow
                            key={row.address}
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                            <TableCell  className={s.address} component="th" scope="row">
                                {row.address}
                            </TableCell>
                            <TableCell align="left">
                                <div className={s.workingHours}>
                                <span  className={s.text}> –ø–Ω-—Å–±</span>
                                <span  className={s.text}> 08:00-19:00</span>
                                <span  className={s.text}> –≤—Å:</span>
                                <span  className={s.text}> 09:00-17:00</span>
                                </div>
                            </TableCell>
                            <TableCell align="left">{row.avaliable}</TableCell>
                            <TableCell align="left">{row.quantity}</TableCell>
                            <TableCell align="left">
                                <button className={s.button}>–ö–£–ü–ò–¢–¨ </button>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}



