import * as React from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import { styled } from '@mui/material/styles';


// —Å—Ç–∏–ª–∏–∑–∞—Ü–∏—è –∑–Ω–∞—á–∫–∞ –≤ –Ω–µ–Ω–∞–∂–∞—Ç–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏!

const BpIcon = styled('span')(({ theme }) => ({

  width: 8,
  height: 8,
  marginRight: 15, 
  outline: '2px auto #C4C4C4',
  outlineOffset: 3,
  

}));

// —Å—Ç–∏–ª–∏–∑–∞—Ü–∏—è –∑–Ω–∞—á–∫–∞ –≤ –Ω–∞–∂–∞—Ç–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏!

const BpCheckedIcon = styled(BpIcon)({
  backgroundColor: '#1C62CD', 
  outline: '2px auto #1C62CD',
  
    display: 'block',
    width: 8,
    height: 8,  
});

// Inspired by blueprintjs
function BpRadio(props) {
  return (
    <Radio
      disableRipple
      // color="default"
      checkedIcon={<BpCheckedIcon />}
      icon={<BpIcon />}
      {...props}
    />
  );
}

export default function RadioButtonMain() {
  return (
    <FormControl component="fieldset">
    
      <RadioGroup row aria-label="gender" name="row-radio-buttons-group">
        <FormControlLabel style={{marginRight:55}}value="female" control={<BpRadio/>} label="–ó–∞–±—Ä–∞—Ç—å —Å–µ–≥–æ–¥–Ω—è" />
        <FormControlLabel value="male" control={<BpRadio/>} label="–ó–∞–±—Ä–∞—Ç—å –≤ —Ç–µ—á–µ–Ω–∏–µ –Ω–µ–¥–µ–ª–∏" />       
        
      </RadioGroup>
    </FormControl>
  );
}
